/* ---------- FONTS ---------- */
/* Roboto */
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap');
/* Capitalized and Bold Font -  Bowlby One SC */
@import url('https://fonts.googleapis.com/css2?family=Bowlby+One+SC&display=swap');
/* Material-UI Font Icons */
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
/* Night at the Opera Theatre font */
@font-face {
  font-family: 'NightAtTheOpera';
  src: local('NightAtTheOpera'), url(./fonts/night-at-the-opera-nf/NightAtTheOperaNF.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
/* Broadyway Font */
@font-face {
  font-family: 'Broady3DFilled';
  src: local('Broady3DFilled'), url(./fonts/broadway/Broadway3DFilled.ttf) format('truetype');
}
@font-face {
  font-family: 'Broadway3D';
  src: local('Broadway3D'), url(./fonts/broadway/Broadway3D.ttf) format('truetype');
}
@font-face {
  font-family: 'Broadway3DItalic';
  src: local('Broadway3DItalic'), url(./fonts/broadway/Broadway3D-Italic.ttf) format('truetype');
}
/* ---------- GENERAL APP STYLING ---------- */
body {
  color: #eeeeee;
  background-color: #16161B;
  padding-bottom: 20px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #040404;
  font-family: 'Broadway3D', sans-serif;
  min-height: 12vh;
  display: flex;
  padding-top: 0.1em;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: 10vw;
  color: white;
}

span.App-header {
  margin-top: 3%;
  display: inline;
}
img#logo-img{
  display: inline;
  /* float: left; */
  margin: 0 2% 2% 0;
  height: 6vmax;
}

/* ---------- SEARCH BAR ---------- */
#movie-search-bar {
  width: 80%;
  font-size: 1.3em;
  font-weight: 500;
  padding: 2%;
  border: block;
  border-radius: 300px;
  margin: 2% 0%;
}

/* #movie-search-bar input:focus{
  outline: none;
  border: 1px solid #64943E;
} */

/* ---------- NOMINATION DRAWER TITLE ----------*/
h2#nomination-list{
  font-family: 'Broadway3D', sans-serif;
}

/* ----------- MOVIE CARDS ------------*/
img.movie-thumbnail {
  height: auto;
  width: 150px;
  border-radius: 5px;
}

/* ---------- NOMINATE BUTTONS ------------*/
button.nominate-btn, button.remove-nominee-btn {
  margin:0 1em 0.2em 1em;
  border-radius: 30px;
}

/* ---------- NOMINATION DRAWER ------------*/
div#nomination-drawer {
  background-color: #383845;
}